<template>
	<div>
		<ul>
			<TodoItem
				v-for="(task, index) of tasks"
				:task="task"
				:key="task.id"
				:index="index"
				v-on:remove-task="removeTask"
			/>
		</ul>
	</div> 
</template>

<script>
import TodoItem from '@/components/TodoItem'
export default {
	props: [`tasks`],
	components: {
		TodoItem,
	},
	methods: {
		removeTask(id) {
			this.$emit(`remove-task`, id)
		} 
	},
}
</script>

<style scoped>

	ul {
		display: flex;
		flex-wrap: wrap;

		list-style: none;
		
		margin: 0;
		padding: 0;
}

@media screen and (max-width: 700px) {
	ul {
		justify-content: center;
	}
}

</style>
