<template>
	<li>
		<span v-bind:class="{done: task.completed}">
	<input type="checkbox" ref="checkbox" v-model="task.completed">
	<strong>{{index + 1}}</strong>
	{{task.title | uppercase}}
		</span>
		<button class="remove-button"
		v-on:click="$emit(`remove-task`, task.id)"
		>&times;</button>
	</li>
</template>

<script>
export default {
	props: {
		task: {
			type: Object,
			required: true,
		},
		index: Number
	},
	filters: {
		uppercase(value) {
			return value[0].toUpperCase() + value.slice(1)
		},
		updated() {
			this.$refs.checkbox.checked = this.task.completed;
		},
	}
}
</script>

<style scoped>
li {
	width: 180px;

	margin: 0px 10px;

	box-shadow:1px 1px 7px rgb(90, 191, 221);
	display: flex;
	flex-direction: column;

	justify-content: space-between;
	/* padding: .5rem 2rem; */
	margin-bottom: 1rem;
}

span {
	padding: 10px;
}

input {
	margin-right: 1rem;
}

.remove-button {
	align-self: flex-end;
	width: 20px;
	height: 20px;
	background:rgb(63, 223, 161);
	border: none;
	color: #fff;
	/* border-radius: 50%; */
	position: relative;
	font-weight: bold;
	outline: none;
}

.done {
	text-decoration: line-through;
}

@media screen and (max-width: 540px) {
	li {
		width: 90%;
	}
}
</style>