<template>
	<form @submit.prevent="onSubmit">
		<input type="text" v-model="title" maxlength="70">
		<button type="submit">Create Task</button>
	</form>
</template>

<script>
export default {
	data() {
		return {
			title: ``
		}
	},
	methods: {
		onSubmit() {
			if (this.title.trim()) {
				const newTask = {
					id: Date.now(),
					title: this.title,
					completed: false,
				}

				this.$emit(`add-task`, newTask)
				this.title = ``
			}
		}
	},
}
</script>

<style scoped>
	form {
		margin-top: 30px;
	}

	button {
		font-size: 20px;
		padding: 5px;
	}

	input {
		font-size: 20px;
		padding: 5px;
		width: 300px;

		margin-right: 15px;
		margin-bottom: 10px;
	};
</style>